.projects-main {
  width: 100%;
  text-align: center;
}

.basic-projects {
  margin-left: 5%;
  margin-right: 5%;
}

.projects-heading-div {
  display: flex;
}

.projects-heading-div > * {
  flex: 1;
}

.projects-heading-img-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.projects-heading-text-div {
  text-align: center;
}

.projects-heading-img-div > * {
  max-width: 60%;
  height: auto;
}

.projects-heading-text {
  font-size: 56px;
  font-weight: 400;
  font-family: "Google Sans Medium";
  line-height: 1.1;
  text-align: center;
  margin-top: 80px;
}

.projects-heading-sub-text {
  font-size: 30px;
  font-family: "Google Sans Regular";
  text-align: center;
  margin-bottom: 10px;
}

.projects-header-detail-text {
  font-size: 20px;
  line-height: 30px;
  margin-top: 0px;
  margin-left: 20px;
  margin-right: 20px;
  font-family: "Google Sans Medium";
}
.publications-heading-text-div {
  text-align: center;
  font-family: "Google Sans Regular";
  margin-top: 70px;
}

.publications-heading-text {
  font-size: 56px;
  font-weight: 600;
}
.repo-cards-div-main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1rem 1rem;
  margin-left: 40px;
  margin-right: 40px;
  margin-top: 60px;
}

/* Solar System Container */
.roller-coaster-container {
  position: relative;
  height: 600px;
  overflow: hidden;
  margin: 50px auto;
  display: block !important;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
}

/* Simple Oval Track - stays behind projects */
.solar-oval-track {
  position: absolute;
  width: 80%;
  height: 60%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  z-index: 1; /* Behind all projects */
  animation: trackGlow 3s ease-in-out infinite alternate;
  pointer-events: none; /* Don't interfere with project clicks */
}

.solar-oval-track::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 95%;
  height: 95%;
  transform: translate(-50%, -50%);
  border: 1px dashed rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  animation: trackRotate 30s linear infinite;
  pointer-events: none; /* Don't interfere with project clicks */
}

/* Project Container */
.project-carousel {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2; /* Above track, but below active projects */
}

/* Center Indicator - stays above normal projects but below active */
.center-indicator {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100; /* Above normal projects but below active project (z-index: 999) */
}

.project-counter {
  background: linear-gradient(45deg, #4a90e2, #357abd);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  box-shadow: 0 4px 12px rgba(74, 144, 226, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.2);
  animation: counterPulse 2s ease-in-out infinite;
}

@keyframes counterPulse {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 0 4px 12px rgba(74, 144, 226, 0.3);
  }
  50% { 
    transform: scale(1.05);
    box-shadow: 0 6px 16px rgba(74, 144, 226, 0.4);
  }
}

/* Project Cards positioned around oval */
.repo-card-hanging {
  position: absolute;
  width: 250px;
  height: auto;
  transition: all 1s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 3;
  transform-origin: center center;
}

/* Active project gets wider for better view */
.repo-card-hanging.active {
  width: 400px; /* Much wider for better readability */
  z-index: 999 !important; /* Highest z-index to be always on top */
}

/* Default state - compact size */
.repo-card-hanging .repo-card-div {
  border-radius: 15px;
  padding: 18px;
  background: linear-gradient(145deg, #ffffff, #f8f9fa) !important;
  border: 2px solid rgba(255, 255, 255, 0.8);
  box-shadow: 
    0 8px 20px rgba(0,0,0,0.1),
    inset 0 1px 0 rgba(255,255,255,0.9);
  transition: all 1s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  opacity: 0.8;
  transform: scale(0.9);
  position: relative;
  overflow: hidden;
}

/* Active project card - wide view with full details */
.repo-card-hanging.active .repo-card-div {
  background: linear-gradient(145deg, #ffffff, #e8f4fd) !important;
  border: 4px solid #1976d2;
  border-radius: 20px;
  padding: 25px;
  box-shadow: 
    0 0 50px rgba(25, 118, 210, 0.4),
    0 25px 50px rgba(0,0,0,0.2),
    inset 0 3px 0 rgba(255,255,255,0.95);
  opacity: 1;
  transform: scale(1.1); /* Slightly smaller scale but wider card */
  position: relative;
  z-index: 1000 !important;
  overflow: visible;
}

/* Enhanced text styling for active project */
.repo-card-hanging.active .repo-name {
  font-size: 24px !important;
  font-weight: 700 !important;
  color: #1976d2 !important;
  margin-bottom: 15px !important;
  line-height: 1.3 !important;
}

.repo-card-hanging.active .repo-description {
  font-size: 16px !important;
  line-height: 1.6 !important;
  color: #333 !important;
  margin: 12px 0 !important;
  max-height: none !important;
  overflow: visible !important;
  display: block !important;
  -webkit-line-clamp: none !important;
}

.repo-card-hanging.active .repo-details {
  margin-top: 18px !important;
  font-size: 14px !important;
}

.repo-card-hanging.active .repo-creation-date {
  font-size: 14px !important;
  color: #666 !important;
  font-weight: 600 !important;
}

/* Enhanced glow effect for wider active card */
.repo-card-hanging.active::before {
  content: '';
  position: absolute;
  top: -25px;
  left: -25px;
  right: -25px;
  bottom: -25px;
  background: radial-gradient(ellipse, 
    rgba(25, 118, 210, 0.3) 0%, 
    rgba(25, 118, 210, 0.15) 50%,
    transparent 100%);
  border-radius: 30px;
  z-index: -2; /* Behind the card */
  animation: activeGlow 5s ease-in-out infinite;
}

/* Wider backdrop for active card */
.repo-card-hanging.active::after {
  content: '';
  position: absolute;
  top: -30px;
  left: -30px;
  right: -30px;
  bottom: -30px;
  background: radial-gradient(ellipse, 
    rgba(0, 0, 0, 0.08) 0%, 
    transparent 70%);
  border-radius: 35px;
  z-index: -3; /* Further behind */
}

.repo-card-hanging:hover .repo-card-div {
  opacity: 1;
  transform: scale(1.05);
  box-shadow: 
    0 12px 25px rgba(0,0,0,0.15),
    inset 0 1px 0 rgba(255,255,255,0.9);
}

/* Remove chains - planets don't need them */

/* Project positions are now dynamically calculated in JavaScript */

/* Simple Animations */

@keyframes trackRotate {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  100% { transform: translate(-50%, -50%) rotate(360deg); }
}

@keyframes trackGlow {
  0% { 
    border-color: rgba(255, 255, 255, 0.3);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
  }
  100% { 
    border-color: rgba(255, 255, 255, 0.6);
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.4);
  }
}

@keyframes activeGlow {
  0% { 
    opacity: 0.3;
    transform: scale(0.95);
  }
  50% { 
    opacity: 0.8;
    transform: scale(1);
  }
  100% { 
    opacity: 0.3;
    transform: scale(0.95);
  }
}

/* Remove old hover effects - now handled by center-active state */

@media (max-width: 1380px) {
  .projects-heading-text {
    font-size: 50px;
    margin-top: 80px;
  }
  .projects-heading-sub-text {
    font-size: 25px;
  }
  
  .roller-coaster-container {
    height: 500px;
  }
  
  .solar-oval-track {
    width: 75%;
    height: 55%;
  }
  
  .repo-card-hanging {
    width: 220px;
  }
  
  .repo-card-hanging.active {
    width: 350px; /* Smaller on tablet */
  }
}

@media (max-width: 768px) {
  .projects-heading-text,
  .publications-heading-text {
    font-size: 30px;
    margin-top: 20px;
  }

  .projects-heading-sub-text {
    font-size: 20px;
  }

  .projects-heading-div {
    flex-direction: column;
  }

  .projects-header-detail-text {
    font-size: 16px;
    margin-right: 0px;
    margin-left: 0px;
    line-height: normal;
    text-align: center;
  }

  .repo-cards-div-main {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 30px;
  }
  
  .roller-coaster-container {
    height: 400px;
    margin: 30px auto;
  }
  
  .solar-oval-track {
    width: 70%;
    height: 50%;
  }
  
  .repo-card-hanging {
    width: 160px;
  }
  
  .repo-card-hanging.active {
    width: 280px; /* Compact on mobile */
  }
  
  .repo-card-hanging .repo-card-div {
    padding: 12px;
    font-size: 12px;
  }
  
  .repo-card-hanging.active .repo-name {
    font-size: 20px !important;
  }
  
  .repo-card-hanging.active .repo-description {
    font-size: 14px !important;
  }
}
