/* Timeline Layout */
.timeline-item {
  position: relative;
  margin-bottom: 2.5rem;
  display: flex;
  align-items: flex-start;
}

.timeline-date-left {
  position: absolute;
  left: -170px;
  top: 15px;
  width: 120px;
  text-align: right;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  line-height: 1.2;
}

.timeline-marker {
  position: absolute;
  left: -40px;
  top: 15px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  z-index: 2;
  border: 4px solid white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}

.timeline-content {
  flex: 1;
  padding: 30px 40px;
  border-radius: 12px;
  border: 1px solid #e0e0e0;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 100%;
  min-height: 140px;
  max-width: none;
}

.timeline-content:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.timeline-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 15px;
  line-height: 1.3;
  font-family: "Google Sans Medium";
}

.timeline-description {
  font-size: 18px;
  line-height: 1.7;
  margin-bottom: 20px;
  max-width: none;
}

.timeline-link {
  font-weight: 600;
  text-decoration: none;
  font-size: 14px;
  transition: all 0.2s ease;
}

.timeline-link:hover {
  text-decoration: none;
  opacity: 0.8;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .timeline-date-left {
    left: -70px;
    width: 60px;
    font-size: 12px;
    top: 12px;
  }

  .timeline-marker {
    left: -32px;
    width: 12px;
    height: 12px;
    top: 12px;
  }
  
  .timeline-content {
    padding: 20px;
    min-height: 100px;
  }
  
  .timeline-title {
    font-size: 18px;
  }
  
  .timeline-description {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .timeline-date-left {
    left: -55px;
    width: 50px;
    font-size: 10px;
    top: 10px;
  }

  .timeline-marker {
    left: -26px;
    width: 10px;
    height: 10px;
    top: 10px;
    border: 3px solid white;
  }

  .timeline-content {
    padding: 15px;
    min-height: 80px;
  }

  .timeline-title {
    font-size: 16px;
  }
  
  .timeline-description {
    font-size: 13px;
  }
}