/* Simple Timeline Layout */
.education-timeline-item {
  position: relative;
  margin-bottom: 2.5rem;
  display: flex;
  align-items: flex-start;
}

.education-timeline-date-left {
  position: absolute;
  left: -170px;
  top: 15px;
  width: 120px;
  text-align: right;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  line-height: 1.2;
}

.education-timeline-marker {
  position: absolute;
  left: -40px;
  top: 15px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  z-index: 2;
  border: 4px solid white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}

.education-timeline-content {
  flex: 1;
  padding: 30px 40px;
  border-radius: 12px;
  border: 1px solid #e0e0e0;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 100%;
  min-height: 140px;
  max-width: none;
}

.education-timeline-content:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.education-card-header {
  margin-bottom: 20px;
}

.university-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 8px;
  line-height: 1.3;
  font-family: "Google Sans Medium";
}

.degree-subtitle {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 15px;
  font-family: "Google Sans Regular";
}

.education-card-body {
  margin-top: 20px;
}

.degree-descriptions {
  margin-bottom: 20px;
}

.degree-description {
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 1.6;
}

.description-bullet {
  margin-right: 10px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.6;
}

.description-text {
  font-family: "Google Sans Regular";
  flex: 1;
}

.education-card-footer {
  margin-top: 20px;
  text-align: left;
}

.visit-website-btn {
  display: inline-block;
  padding: 8px 16px;
  border: 2px solid;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.2s ease;
}

.visit-website-btn:hover {
  background-color: rgba(0, 0, 0, 0.05);
  text-decoration: none;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .education-timeline-date-left {
    left: -70px;
    width: 60px;
    font-size: 12px;
    top: 12px;
  }

  .education-timeline-marker {
    left: -32px;
    width: 12px;
    height: 12px;
    top: 12px;
  }
  
  .education-timeline-content {
    padding: 20px;
    min-height: 100px;
  }
  
  .university-title {
    font-size: 20px;
  }
  
  .degree-subtitle {
    font-size: 16px;
  }
  
  .degree-description {
    font-size: 14px;
  }
}