.admin-login-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-family: 'Google Sans Regular', sans-serif;
}

.login-wrapper {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 16px;
  padding: 40px;
  max-width: 480px;
  width: 100%;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.login-header {
  text-align: center;
  margin-bottom: 40px;
}

.login-logo {
  font-size: 64px;
  margin-bottom: 16px;
  display: block;
}

.login-header h1 {
  color: #1e3a8a;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 8px;
  font-family: 'Google Sans Medium', sans-serif;
}

.login-header p {
  color: #6b7280;
  font-size: 16px;
  margin: 0;
}

.login-form {
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 24px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #374151;
  font-size: 14px;
}

.form-input {
  width: 100%;
  padding: 14px 16px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 16px;
  transition: all 0.3s ease;
  background: #ffffff;
}

.form-input:focus {
  outline: none;
  border-color: #1e3a8a;
  box-shadow: 0 0 0 4px rgba(30, 58, 138, 0.1);
}

.form-input:disabled {
  background: #f9fafb;
  color: #6b7280;
  cursor: not-allowed;
}

.password-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-toggle {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  color: #6b7280;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.password-toggle:hover {
  background: #f3f4f6;
  color: #374151;
}

.password-toggle:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.error-message {
  background: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
}

.login-button {
  width: 100%;
  background: #1e3a8a;
  color: white;
  border: none;
  padding: 16px 24px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.login-button:hover:not(:disabled) {
  background: #1e40af;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(30, 58, 138, 0.3);
}

.login-button:disabled {
  background: #9ca3af;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.login-footer {
  border-top: 1px solid #e5e7eb;
  padding-top: 24px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
}

.login-info h4,
.login-help h4 {
  color: #1e3a8a;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 12px;
  font-family: 'Google Sans Medium', sans-serif;
}

.login-info ul,
.login-help ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.login-info li,
.login-help li {
  color: #4b5563;
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  gap: 6px;
}

/* Responsive Design */
@media (max-width: 640px) {
  .login-wrapper {
    padding: 24px;
    margin: 10px;
  }

  .login-header h1 {
    font-size: 24px;
  }

  .login-logo {
    font-size: 48px;
  }

  .login-footer {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .login-wrapper {
    background: rgba(31, 41, 55, 0.95);
    color: #f9fafb;
  }

  .form-input {
    background: #374151;
    border-color: #4b5563;
    color: #f9fafb;
  }

  .form-input:focus {
    border-color: #60a5fa;
    box-shadow: 0 0 0 4px rgba(96, 165, 250, 0.1);
  }

  .form-group label {
    color: #d1d5db;
  }

  .login-header p {
    color: #9ca3af;
  }

  .login-info li,
  .login-help li {
    color: #d1d5db;
  }
}