.news-admin-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Google Sans Regular', sans-serif;
}

.news-admin-header {
  margin-bottom: 40px;
  padding-bottom: 20px;
  border-bottom: 2px solid #e5e7eb;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-title {
  flex: 1;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 16px;
}

.user-info {
  color: #6b7280;
  font-size: 14px;
}

.logout-btn {
  background: #dc2626;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: background-color 0.2s ease;
}

.logout-btn:hover {
  background: #b91c1c;
}

.news-admin-header h1 {
  color: #1e3a8a;
  font-size: 32px;
  margin-bottom: 8px;
}

.news-admin-header p {
  color: #6b7280;
  font-size: 16px;
}

.news-admin-loading {
  text-align: center;
  padding: 60px;
  font-size: 18px;
  color: #6b7280;
}

/* News List Styles */
.list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.list-header h2 {
  color: #1e3a8a;
  margin: 0;
}

.create-btn {
  background: #059669;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background-color 0.2s ease;
}

.create-btn:hover {
  background: #047857;
}

.news-table {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.news-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  background: #f9fafb;
  transition: all 0.2s ease;
}

.news-row:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-color: #1e3a8a;
}

.news-info {
  flex: 1;
}

.news-title {
  font-size: 18px;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 8px;
}

.news-meta {
  display: flex;
  gap: 16px;
  margin-bottom: 8px;
}

.news-date {
  color: #6b7280;
  font-size: 14px;
}

.news-category {
  background: #dbeafe;
  color: #1e40af;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
}

.news-source {
  background: #f3f4f6;
  color: #4b5563;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 12px;
}

.news-description-preview {
  color: #6b7280;
  font-size: 14px;
  line-height: 1.4;
}

.news-actions {
  display: flex;
  gap: 8px;
}

.edit-btn {
  background: #1e3a8a;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s ease;
}

.edit-btn:hover {
  background: #1e40af;
}

/* Edit Form Styles */
.news-edit-form {
  max-width: 800px;
  margin: 0 auto;
}

.form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border-bottom: 1px solid #e5e7eb;
}

.form-header h2 {
  color: #1e3a8a;
  margin: 0;
}

.back-btn {
  background: #6b7280;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  text-decoration: none;
}

.back-btn:hover {
  background: #4b5563;
}

.form-group {
  margin-bottom: 24px;
}

.form-group label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
  color: #374151;
}

.form-input,
.form-select,
.form-textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  font-size: 16px;
  font-family: inherit;
  transition: border-color 0.2s ease;
}

.form-input:focus,
.form-select:focus,
.form-textarea:focus {
  outline: none;
  border-color: #1e3a8a;
  box-shadow: 0 0 0 2px rgba(30, 58, 138, 0.1);
}

.form-textarea {
  min-height: 200px;
  resize: vertical;
}

.form-actions {
  text-align: center;
  margin-top: 30px;
}

.generate-btn {
  background: #059669;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  transition: background-color 0.2s ease;
}

.generate-btn:hover {
  background: #047857;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 12px;
  max-width: 700px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e5e7eb;
}

.modal-header h3 {
  margin: 0;
  color: #1e3a8a;
}

.close-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #6b7280;
  padding: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  background: #f3f4f6;
}

.modal-body {
  padding: 20px;
}

.modal-body ol {
  background: #f9fafb;
  padding: 16px;
  border-radius: 6px;
  margin-bottom: 20px;
}

.modal-body code {
  background: #e5e7eb;
  padding: 2px 4px;
  border-radius: 3px;
  font-family: 'Monaco', 'Consolas', monospace;
}

.issue-preview {
  background: #1f2937;
  color: #f9fafb;
  padding: 16px;
  border-radius: 6px;
  margin: 16px 0;
  font-family: 'Monaco', 'Consolas', monospace;
  font-size: 13px;
  overflow-x: auto;
}

.issue-preview pre {
  margin: 0;
  white-space: pre-wrap;
}

.modal-actions {
  display: flex;
  gap: 12px;
  justify-content: center;
}

.copy-btn,
.github-btn {
  padding: 10px 20px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 600;
  cursor: pointer;
  border: none;
  font-size: 14px;
  transition: all 0.2s ease;
}

.copy-btn {
  background: #6b7280;
  color: white;
}

.copy-btn:hover {
  background: #4b5563;
}

.github-btn {
  background: #1e3a8a;
  color: white;
}

.github-btn:hover {
  background: #1e40af;
}

/* Responsive Design */
@media (max-width: 768px) {
  .news-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }

  .news-actions {
    align-self: flex-end;
  }

  .form-header {
    flex-direction: column;
    gap: 12px;
    text-align: center;
  }

  .modal-content {
    width: 95%;
    margin: 10px;
  }

  .modal-actions {
    flex-direction: column;
  }
}