.experience-main {
  width: 100%;
}

.basic-experience {
  margin-left: 5%;
  margin-right: 5%;
}

/* Simple Timeline Styles */
.timeline-container {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
  padding: 40px 0;
  padding-left: 180px;
}

/* Simple vertical line */
.timeline-container::before {
  content: '';
  position: absolute;
  left: 140px;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(to bottom, transparent 0%, #ddd 10%, #ddd 90%, transparent 100%);
}

.timeline-item {
  position: relative;
  margin-bottom: 2.5rem;
  display: flex;
  align-items: flex-start;
}

.timeline-date-left {
  position: absolute;
  left: -170px;
  top: 15px;
  width: 120px;
  text-align: right;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  line-height: 1.2;
}

.timeline-marker {
  position: absolute;
  left: -40px;
  top: 15px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  z-index: 2;
  border: 4px solid white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}

.timeline-content {
  flex: 1;
  padding: 30px 40px;
  border-radius: 12px;
  border: 1px solid #e0e0e0;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 100%;
  min-height: 140px;
  max-width: none;
}

.timeline-content:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.12);
}

.timeline-card-header {
  display: flex;
  align-items: flex-start;
  margin-bottom: 15px;
}

.timeline-logo {
  margin-right: 20px;
  flex-shrink: 0;
}

.timeline-logo-img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.timeline-logo-img:hover {
  transform: scale(1.1);
}

.timeline-details {
  flex: 1;
}

.timeline-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 15px;
  line-height: 1.3;
  font-family: "Google Sans Medium";
}

.timeline-company {
  font-family: "Google Sans Regular";
  font-size: 18px;
  margin: 0 0 10px 0;
  font-weight: 500;
}

.timeline-company a {
  font-weight: 600;
  text-decoration: none;
  font-size: 16px;
  transition: all 0.2s ease;
}

.timeline-company a:hover {
  text-decoration: none;
  opacity: 0.8;
}

.timeline-location {
  font-family: "Google Sans Regular";
  font-size: 14px;
  margin-bottom: 10px;
}

.timeline-description {
  font-size: 18px;
  line-height: 1.7;
  margin-bottom: 20px;
  max-width: none;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .timeline-container {
    padding-left: 70px;
  }

  .timeline-date-left {
    left: -70px;
    width: 60px;
    font-size: 12px;
    top: 12px;
  }

  .timeline-marker {
    left: -32px;
    width: 12px;
    height: 12px;
    top: 12px;
  }
  
  .timeline-content {
    padding: 20px;
    min-height: 100px;
  }
  
  .timeline-title {
    font-size: 18px;
  }
  
  .timeline-description {
    font-size: 16px;
  }

  .timeline-logo-img {
    width: 50px;
    height: 50px;
  }
}

.experience-heading-div {
  display: flex;
}

.experience-heading-div > * {
  flex: 1;
}

.experience-heading-img-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.experience-heading-text-div {
  text-align: center;
}

.experience-heading-img-div > * {
  max-width: 100%;
  height: auto;
}

.experience-heading-text {
  font-size: 56px;
  font-weight: 400;
  font-family: "Google Sans Medium";
  line-height: 1.1;
  text-align: center;
  margin-top: 80px;
}

.experience-heading-sub-text {
  font-size: 30px;
  font-family: "Google Sans Regular";
  text-align: center;
  margin-bottom: 10px;
}

.experience-header-detail-text {
  font-size: 20px;
  line-height: 30px;
  margin-top: 0px;
  margin-left: 20px;
  margin-right: 20px;
  font-family: "Google Sans Medium";
}

@media (max-width: 1380px) {
  .experience-heading-text {
    font-size: 50px;
    margin-top: 80px;
  }
  .experience-heading-sub-text {
    font-size: 25px;
  }
}

@media (max-width: 768px) {
  .experience-heading-text {
    font-size: 30px;
    margin-top: 20px;
  }

  .experience-heading-sub-text {
    font-size: 20px;
  }

  .experience-heading-div {
    flex-direction: column;
  }

  .experience-header-detail-text {
    font-size: 16px;
    margin-right: 0px;
    line-height: normal;
    text-align: center;
  }
}

.experience-card {
  min-width: 350px;
  /* height: 170px; */
  margin-bottom: 20px;
  border-radius:10px;
  display: flex;
  flex-direction:column;
  padding: 10px;
  margin-left: 10px;
  -webkit-box-shadow: 4px 3px 20px -2px grey;
  -moz-box-shadow: 4px 3px 20px -2px grey;
  box-shadow: 4px 3px 20px -2px grey;
}

.arrow-left {
  width: 0;
  height: 0;
  margin-top: 40px;
  margin-left: 10px;
  margin-right: -10px;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  z-index: 100;
}

.experience-card-logo-div {
  margin: 10px;
  margin-top: -10px;
}

.experience-card-stepper {
  display: flex;
  flex-direction:row;
  position: relative;
  align-items: center; 
  flex-direction: column; 
  margin-top: -40px
}

.experience-card-body-div {
  width: 100%;
}

.experience-card-header-div {
  display: flex;
  flex-direction: row;
}

.experience-card-logo {
  width: 70px;
  -webkit-box-shadow: 4px 3px 20px -2px grey;
  -moz-box-shadow: 4px 3px 20px -2px grey;
  box-shadow: 4px 3px 20px -2px grey;
  border-radius: 50px;
}

.experience-card-title {
  font-family: "Google Sans Regular";
  margin-top: 10px;
  margin-bottom: 2px;
  margin-left: 0px;
  margin-right: 7px;
  text-align: left;
}

.experience-card-company {
  font-family: "Google Sans Regular";
  margin: 0;
  text-align: left;
}

.experience-card-duration {
  font-family: "Google Sans Regular";
  margin-top: 10px;
  margin-bottom: 2px;
  margin-left: 0px;
  margin-right: 0px;
  text-align: right;
  color: #868e96;
}

.experience-card-heading-left {
  float: left;
  margin-left: 10px;
  margin-right: auto;
}

.experience-card-heading-right {
  float: right;
  margin-left: auto;
  margin-right: 10px;
}

.experience-card-location {
  font-family: "Google Sans Regular";
  margin: 0;
  text-align: right;
  color: #868e96;
}

.experience-card-description {
  margin-top: 5px;
  margin-left: 10px;
  text-align: justify;
  margin-right: 20px;
  font-family: "Google Sans Regular";
}


.experience-card-company > a {
  position: relative;
  color: #000;
  text-decoration: none;
}

.experience-card-company > a:hover {
  color: #000;
} 

.experience-card-company > a:hover:before {
visibility: visible;
-webkit-transform: scaleX(1);
transform: scaleX(1);
}

.experience-card-company > a:before {
content: "";
position: absolute;
width: 100%;
height: 1px;
bottom: 0;
left: 0;
background-color: #000;
visibility: hidden;
-webkit-transform: scaleX(0);
transform: scaleX(0);
-webkit-transition: all 0.3s ease-in-out 0s;
transition: all 0.3s ease-in-out 0s;
} 

.experience-list-item {
  display: flex;
  align-items: center;
  flex-direction: row;
}

