/* Skills Galaxy Container */
.skills-galaxy-container {
  width: 100%;
  min-height: 100vh;
  padding: 60px 20px;
  position: relative;
  overflow: hidden;
}

.galaxy-header {
  text-align: center;
  margin-bottom: 80px;
}

.galaxy-title {
  font-size: 48px;
  font-weight: 700;
  font-family: "Google Sans Medium";
  margin-bottom: 16px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.galaxy-subtitle {
  font-size: 18px;
  font-family: "Google Sans Regular";
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Galaxy Main Container */
.galaxy-main {
  position: relative;
  width: 100%;
  max-width: 1000px;
  height: 800px;
  margin: 0 auto;
  border-radius: 50%;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: visible;
}

/* Central Hub */
.galaxy-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 10;
}

.center-avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 4px solid;
  margin: 0 auto 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.center-avatar:hover {
  transform: scale(1.1);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.center-content h2 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 4px;
  font-family: "Google Sans Medium";
  text-align: center;
  line-height: 1.2;
}

.center-content p {
  font-size: 14px;
  font-family: "Google Sans Regular";
  text-align: center;
  margin: 0;
}

/* Core Skills Ring */
.core-skills-ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 420px;
  height: 420px;
}

.core-skill {
  position: absolute;
  width: 160px;
  height: 50px;
  top: 50%;
  left: 50%;
  transform-origin: 50% 0;
  transform: translate(-50%, -210px) rotate(var(--core-angle));
}

.core-skill-content {
  transform: rotate(calc(-1 * var(--core-angle)));
  text-align: center;
  padding: 8px 12px;
  background: rgba(30, 58, 138, 0.1);
  border-radius: 20px;
  border: 2px solid rgba(30, 58, 138, 0.2);
  transition: all 0.3s ease;
}

.core-skill-content:hover {
  background: rgba(30, 58, 138, 0.2);
  transform: rotate(calc(-1 * var(--core-angle))) scale(1.05);
  border-color: rgba(30, 58, 138, 0.4);
}

.core-skill-content h3 {
  font-size: 13px;
  font-weight: 600;
  margin: 0;
  font-family: "Google Sans Medium";
  line-height: 1.2;
  white-space: nowrap;
}

/* Orbital Skills */
.orbital-skills {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}

.orbit {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  pointer-events: none;
}

.orbit-1 {
  width: 320px;
  height: 320px;
  animation: orbit-glow-1 4s ease-in-out infinite alternate;
}

.orbit-2 {
  width: 480px;
  height: 480px;
  animation: orbit-glow-2 5s ease-in-out infinite alternate;
}

.orbit-3 {
  width: 640px;
  height: 640px;
  animation: orbit-glow-3 6s ease-in-out infinite alternate;
}

@keyframes orbit-glow-1 {
  0% { box-shadow: 0 0 20px rgba(102, 126, 234, 0.3); }
  100% { box-shadow: 0 0 40px rgba(102, 126, 234, 0.5); }
}

@keyframes orbit-glow-2 {
  0% { box-shadow: 0 0 15px rgba(118, 75, 162, 0.2); }
  100% { box-shadow: 0 0 30px rgba(118, 75, 162, 0.4); }
}

@keyframes orbit-glow-3 {
  0% { box-shadow: 0 0 10px rgba(255, 111, 0, 0.1); }
  100% { box-shadow: 0 0 20px rgba(255, 111, 0, 0.3); }
}

/* Orbital Skill Items */
.skill-orbit-item {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  transform-origin: 0 0;
  transform: 
    translate(-30px, -30px) 
    rotate(var(--orbit-angle)) 
    translateX(var(--orbit-radius)) 
    rotate(calc(-1 * var(--orbit-angle)));
  animation: orbit-rotate var(--orbit-duration) linear infinite;
  pointer-events: all;
  z-index: 5;
}

.skill-orbit-item.paused {
  animation-play-state: paused;
}

@keyframes orbit-rotate {
  from { 
    transform: 
      translate(-30px, -30px) 
      rotate(var(--orbit-angle)) 
      translateX(var(--orbit-radius)) 
      rotate(calc(-1 * var(--orbit-angle)));
  }
  to { 
    transform: 
      translate(-30px, -30px) 
      rotate(calc(var(--orbit-angle) + 360deg)) 
      translateX(var(--orbit-radius)) 
      rotate(calc(-1 * (var(--orbit-angle) + 360deg)));
  }
}

.skill-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  backdrop-filter: blur(10px);
  position: relative;
  background: rgba(255, 255, 255, 0.9);
}

.skill-icon img {
  width: 32px;
  height: 32px;
  object-fit: contain;
  border-radius: 4px;
}

.skill-icon:hover,
.skill-icon.hovered {
  transform: scale(1.3);
  box-shadow: 0 0 25px var(--skill-color);
  z-index: 10;
}

.skill-name {
  position: absolute;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 12px;
  font-weight: 600;
  white-space: nowrap;
  opacity: 1;
  pointer-events: none;
  font-family: "Google Sans Medium";
}

/* Skill Tooltip */
.skill-tooltip {
  position: absolute;
  top: -100px;
  left: 50%;
  transform: translateX(-50%);
  padding: 12px 16px;
  border-radius: 8px;
  border: 1px solid;
  backdrop-filter: blur(10px);
  white-space: nowrap;
  z-index: 20;
  pointer-events: none;
  animation: tooltip-appear 0.3s ease;
}

@keyframes tooltip-appear {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.skill-tooltip strong {
  display: block;
  margin-bottom: 4px;
  font-size: 14px;
}

.skill-tooltip p {
  margin: 0;
  font-size: 12px;
}

/* Skill Description Panel */
.skill-description-panel {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 20px 30px;
  border-radius: 12px;
  border: 2px solid;
  backdrop-filter: blur(10px);
  text-align: center;
  min-width: 300px;
  z-index: 15;
}

.skill-description-panel h3 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 8px;
  font-family: "Google Sans Medium";
}

.skill-description-panel p {
  font-size: 14px;
  line-height: 1.5;
  margin: 0;
}

/* Awards Section */
.awards-section {
  margin-top: 120px;
  padding: 60px 0;
}

.awards-title {
  text-align: center;
  font-size: 36px;
  font-weight: 700;
  font-family: "Google Sans Medium";
  margin-bottom: 50px;
}

.awards-timeline {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.award-badge {
  padding: 25px;
  border-radius: 12px;
  border: 2px solid #e5e7eb;
  background: #f9fafb;
  transition: all 0.3s ease;
  animation: award-float 3s ease-in-out infinite;
  animation-delay: var(--award-delay);
  position: relative;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  min-height: 220px;
  display: flex;
  flex-direction: column;
}

.award-badge::before {
  display: none;
}

@keyframes award-float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes border-glow {
  0% { opacity: 0.5; }
  100% { opacity: 1; }
}

.award-badge:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  border-color: #1e3a8a;
}

.award-icon {
  text-align: center;
  margin-bottom: 16px;
}

.award-year {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 6px;
  font-family: "Google Sans Medium";
}

.award-title {
  font-size: 16px;
  font-weight: 600;
  margin: 0;
  font-family: "Google Sans Medium";
  line-height: 1.3;
}

.award-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.award-description {
  flex: 1;
}

.award-details {
  border-left: 3px solid var(--award-color);
  padding-left: 8px;
  margin: 8px 0;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .galaxy-main {
    height: 700px;
  }

  .orbit-3 {
    width: 560px;
    height: 560px;
  }
}

@media (max-width: 768px) {
  .galaxy-title {
    font-size: 36px;
  }

  .galaxy-main {
    height: 600px;
    max-width: 90%;
  }

  .orbit-1 { width: 280px; height: 280px; }
  .orbit-2 { width: 400px; height: 400px; }
  .orbit-3 { width: 480px; height: 480px; }

  .core-skill-content {
    padding: 8px;
  }

  .core-skill-content h3 {
    font-size: 12px;
  }

  .core-skill-content p {
    font-size: 9px;
  }

  .skill-icon {
    width: 50px;
    height: 50px;
  }

  .skill-name {
    font-size: 10px;
    top: 60px;
  }

  .awards-timeline {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .galaxy-main {
    height: 500px;
  }

  .orbit-1 { width: 240px; height: 240px; }
  .orbit-2 { width: 320px; height: 320px; }
  .orbit-3 { width: 400px; height: 400px; }

  .center-avatar {
    width: 80px;
    height: 80px;
  }

  .center-content h2 {
    font-size: 18px;
  }

  .center-content p {
    font-size: 12px;
  }

  .skill-description-panel {
    min-width: 250px;
    padding: 15px 20px;
  }
}