.publications-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 20px;
}


.publications-header-section {
  margin-top: 30px;
  margin-bottom: 30px;
}

.publications-heading-div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  margin-top: 0;
  margin-bottom: 0;
}

.publications-heading-img-div {
  flex: 0 0 350px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.publications-illustration {
  width: 320px;
  max-width: 100%;
  height: auto;
}

.publications-heading-text-div {
  flex: 1;
  text-align: left;
}

.publications-heading-text {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 20px;
}

.publications-header-detail-text {
  font-size: 1.4rem;
  line-height: 1.6;
  max-width: 900px;
  margin: 0 auto;
  opacity: 0.8;
  text-align: center;
}




.publications-body-div {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 30px;
  margin-bottom: 50px;
}

/* Responsive Design */

@media (max-width: 900px) {
  .publications-heading-div {
    flex-direction: column;
    gap: 20px;
    margin-top: 0;
    margin-bottom: 0;
  }
  .publications-heading-img-div {
    margin-bottom: 10px;
  }
  .publications-heading-text-div {
    text-align: center;
  }
  .publications-heading-text {
    font-size: 2.2rem;
  }
  .publications-header-detail-text {
    font-size: 1.1rem;
    padding: 0 20px;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .publications-header h1 {
    font-size: 2rem;
  }
  
  .publications-header {
    padding: 40px 0 30px 0;
  }
}